@import '../../style/variables.scss';

.eventsContainer {
    max-width: 730px;
    background-color: $purple86;
    border-radius: 10px;
    margin: 0 auto;
    color: #ffffff;
    padding: 65px 30px;
    animation: slideIn 1s;
    animation-fill-mode: forwards;
    transition: transform 1s;
    :global(.isOut) & {
        animation: slideOut 1s;
    }
}

@keyframes slideOut {
    0% {
        transform: translateY(0);
    }
    100% {
        transform: translateY(200%);
    }
}
@keyframes slideIn {
    0% {
        transform: translateY(200%);
    }
    100% {
        transform: translateY(0);
    }
}

.colWrapper {
    position: relative;
    display: flex;
    justify-content: space-between;
    &:after {
        display: block;
        content: '';
        position: absolute;
        background-color: #fff;
        width: 2px;
        top: 70px;
        bottom: 20px;
        transform: translateX(-50%);
        left: calc(50% - 10px);
    }
}

.col {
    flex-basis: 50%;
    &:first-child {
        padding-left: 120px;
        .eventListEl {
            &:after {
                position: absolute;
                display: block;
                content: '';
                width: 20px;
                height: 20px;
                border: 4px solid #fff;
                background-color: rgb(126, 87, 164);
                border-radius: 50%;
                top: 10px;
                right: -10px;
                transform: translate(-50%, -50%);
                z-index: 1;
            }
        }
    }
    &:last-child {
        padding-left: 20px;
    }
}

.colTitle {
    font-size: 32px;
    line-height: 40px;
    font-weight: $bold;
    margin-bottom: 30px;
}

.eventListEl {
    position: relative;
    font-size: 24px;
    line-height: 1;
    &:not(:last-child) {
        min-height: 64px;
    }
}

.columnEl {
    display: flex;
    justify-content: space-between;
}





